.home_container{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4em;
}

.home_container img{
    margin-top: -0px;
    margin-left: -450px;
    width: 350px;
}

.home_container h1{
    font-size: 2.5em;
    margin-bottom: .5em;
}

.home_container h1 span{
    color: #ffbb33;
    padding: 0 0.2em;
    background-color: #222;
}

.home_container p{
    margin-bottom: 1.5em;
}

.btn{
    background-color: #222;
    color: #fff;
    padding: 0.5em 1em;
    text-decoration: none;
    transition: 0.5s;
}

.btn:hover{
    color: #ffbb33;
}